import UserInputArea from "./userinput/UserInput";
import { IoReloadOutline } from "react-icons/io5";
import React from "react";
import "./app.css";

function App() {
  const BLUR_AMOUNT = "5px";
  const MAX_NAME_LENGTH = "20";

  const questionBoxRef = React.useRef();

  const [username, setUsername] = React.useState("");

  const [promptType, setPromptType] = React.useState("a");

  const [blur, setBlur] = React.useState(true);

  const [qid, setQid] = React.useState(null);

  const [answers, setAnswers] = React.useState({});

  const [reloadButtonClass, setReloadButtonClass] =
    React.useState("reload-button");

  const handleServerRes = (res) => {
    console.log(res.status);

    if (res.status === 200) {
      res.json().then((res) => {
        console.log(res);
        if (res.promptType === "a") {
          res.sensitive ? setBlur(true) : setBlur(false);
          questionBoxRef.current.innerHTML = `"${res.prompt}?"`;
          setPromptType("a");
          setQid(res.qid);
        } else if (res.promptType === "q") {
          setBlur(false);
          questionBoxRef.current.innerHTML = `${res.prompt}`;
          setPromptType("q");
          setQid(res.qid);
        }
      });
    }
  };

  React.useEffect(() => {
    // if (usernameCookie) {
    //   const decodedCookie = escape(window.atob(usernameCookie));
    //   const sessionSplits = decodedCookie.split(":");
    //   sessionSplits.find((val, index) => {
    //     console.log(val);
    //     if (val.includes("username")) {
    //       setUsername(sessionSplits[index + 1].split(`,`)[0].replace(`"`, ""));
    //       return true;
    //     }
    //     return false;
    //   });
    //   setUsername(usernameCookie.split("=")[1]);
    // }

    // window.username = "Evin";
    if (window.username) setUsername(window.username);

    if (window.promptType && window.prompt) {
      setPromptType(window.promptType);
      setQid(window.qid);
      if (window.promptType === "q")
        questionBoxRef.current.innerHTML = window.prompt;
      else questionBoxRef.current.innerHTML = `"${window.prompt}?"`;
    }

    if (window.username && !window.sensitive) setBlur(false);

    fetch("/api/answers")
      .then((resp) => resp.json().then((data) => setAnswers(data)))
      .catch(() => console.log("QnApp: Failed to fetch answers"));

    document.addEventListener("keydown", handleInteract);

    document.addEventListener("touchend", (se) => {
      se.preventDefault();
      handleInteract(se);
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleInteract = (se) => {
    document.querySelector(".input-bar").focus();
  };

  const handleAnswersReload = (se) => {
    setReloadButtonClass("reload-button disabled");
    setTimeout(() => {
      setReloadButtonClass("reload-button");
    }, 3000);

    fetch("/api/answers")
      .then((resp) => resp.json().then((data) => setAnswers(data)))
      .catch(() => console.log("QnApp: Failed to fetch answers"));
  };

  const handleNameSubmit = (se) => {
    se.preventDefault();
    const nameInput = document.querySelector(".input-bar").value.trim();

    if (
      nameInput.length > 0 &&
      // !nameInput.match(/[!@#$%^&*()_+\-=[\]{};':"\\|,.<>/?]/)
      !nameInput.match(/[\p{P}\p{S}]/u)
    ) {
      const requestOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ username: nameInput }),
      };

      fetch("api/name", requestOptions)
        .then((response) =>
          response.status === 200
            ? setUsername(nameInput)
            : (document.querySelector(".name-input-error").innerHTML =
                "Something went wrong trying to connect... Please reload")
        )
        .catch(
          () =>
            (document.querySelector(".name-input-error").innerHTML =
              "Could not connect to server")
        );
    }
    document.querySelector(".name-input-error").hidden = false;

    document.querySelector(".input-bar").value = "";
  };

  return (
    <div className="App">
      {!username && (
        <div className="name-input-screen" hidden={username}>
          <form className="name-input-container" onSubmit={handleNameSubmit}>
            <span className="name-input-prompt">What is your name?</span>
            <input
              className="input-bar"
              id="name-input"
              type="text"
              maxLength={MAX_NAME_LENGTH}
            />
            <span className="name-input-error" hidden={true}>
              Name cannot contain special characters
            </span>
          </form>
        </div>
      )}

      {username && <div className="name-display">{username}</div>}
      <div
        style={{
          alignSelf: "center",
          marginBottom: "15rem",
          color: "white",
          filter: `blur(${blur ? BLUR_AMOUNT : 0})`,
        }}
        onMouseEnter={() => setBlur(false)}
        ref={questionBoxRef}
      >
        *Imagine a question*
      </div>
      <UserInputArea
        style={{ filter: `blur(${username ? 0 : BLUR_AMOUNT})` }}
        onServerResponse={handleServerRes}
        promptType={promptType}
        qid={qid}
      />
      <div
        className="answers-tab"
        onMouseLeave={(se) => se.target.scrollTo(0, 0)}
      >
        <IoReloadOutline
          className={reloadButtonClass}
          onClick={handleAnswersReload}
        />
        {Object.keys(answers)
          .reverse()
          .map((value, idx) => {
            const currQuestion = answers[`${value}`];
            const likes = currQuestion.likes;
            const dislikes = currQuestion.dislikes;
            const totalResponses =
              likes + dislikes === 0 ? 2 : likes + dislikes;

            return (
              <div key={value} className="answer-element">
                <div className="answer-info">
                  <b>"{currQuestion.content}"</b>: {currQuestion.likes} likes/{" "}
                  {currQuestion.dislikes} dislikes
                </div>
                <div
                  className="dislikes-bar response-bar"
                  style={{
                    width: "35rem",
                  }}
                ></div>
                <div
                  className="likes-bar response-bar"
                  style={{
                    width: `${
                      likes > dislikes
                        ? 17.5 + ((likes / totalResponses) * 17.5) / 2
                        : 17.5 - ((likes / totalResponses) * 17.5) / 2
                    }rem`,
                    zIndex: "20",
                  }}
                ></div>
                {currQuestion.answers.map((data) => (
                  <div className="question-response">"{data}"</div>
                ))}
              </div>
            );
          })}
      </div>
    </div>
  );
}

export default App;
