import React from "react";
// import AnswerRateBar from "./AnswerRateBar";
import { IoMdThumbsUp, IoMdThumbsDown, IoMdFlag } from "react-icons/io";
import { HiArrowNarrowRight } from "react-icons/hi";
import "./userinput.css";

const maxQuestionLength = process.env.MAX_QUESTION_LENGTH || 60;
const minBoxLength = process.env.MIN_BOX_LENGTH || 6;

const errorPersistDuration = 2000;
const errorFadeDuration = 500;

const errorShakeDuration = 300;

const UserInput = ({
  onServerResponse = null,
  style = {},
  promptType = "a",
  qid = 0,
}) => {
  const [inputText, setInputText] = React.useState("");

  const [showError, setShowError] = React.useState(false);

  const [isHidden, setIsHidden] = React.useState(true);

  const [goButtonClass, setGoButtonClass] = React.useState("go-button");

  const [timeoutId, setTimeoutId] = React.useState(null);

  const [hiddenTimeoutId, setHiddenTimeoutId] = React.useState(null);

  const [shakeTimeoutId, setShakeTimeoutId] = React.useState(null);

  const [rateState, setRateState] = React.useState({
    rated: false,
    liked: false,
    reported: false,
  });

  const errorMessageRef = React.useRef();

  const handleUserType = (se) => {
    setInputText(se.target.value);
  };

  // The rating system is controlled by the rateState state
  const handleRate = (data) => {
    // if reported is passed in as true, then it means the user has clicked
    // the report button
    if (data.reported) {
      setRateState((prevRateState) => {
        const rated = prevRateState.rated;
        const liked = prevRateState.liked;
        const reported = !prevRateState.reported;
        return { rated: rated, liked: liked, reported: reported };
      });
    }
    // if it's not the report button then it must either be the like or dislike
    // button
    else {
      setRateState((prevRateState) => {
        // if the user's already rated and this rating is the same as the rating
        // stored in the rateState, that means the user wants to cancel that rating
        // by clicking the same button twice, so we should change rated to false
        // and liked to false

        const sameLikedState = prevRateState.liked === data.liked;

        if (prevRateState.rated && sameLikedState) {
          const rated = !prevRateState.rated;
          const liked = false;
          const reported = prevRateState.reported;

          return { rated: rated, liked: liked, reported: reported };
        } else {
          // else the user is trying to like/dislike something for the first time and
          // this should trigger the default behaviour of setting rated to false and
          // liked to whatever the user passed in
          const liked = data.liked;
          const rated = true;
          const reported = prevRateState.reported;

          return { rated: rated, liked: liked, reported: reported };
        }
      });
    }
  };

  const throwInputError = (message = "Error") => {
    errorMessageRef.current.innerHTML = message;

    setGoButtonClass("go-button error");
    setShowError(true);
    setIsHidden(false);

    // if (shakeTimeoutId) clearTimeout(shakeTimeoutId);

    if (timeoutId) clearTimeout(timeoutId);

    if (hiddenTimeoutId) clearTimeout(hiddenTimeoutId);

    if (!shakeTimeoutId) {
      setShakeTimeoutId(
        setTimeout(() => {
          setShakeTimeoutId(null);
          setGoButtonClass("go-button");
        }, errorShakeDuration + 5)
      );
    }
    setTimeoutId(
      setTimeout(() => {
        setTimeoutId(null);
        setShowError(false);
      }, errorPersistDuration)
    );

    setHiddenTimeoutId(
      setTimeout(() => {
        setIsHidden(true);
        setHiddenTimeoutId(null);
      }, errorPersistDuration + errorFadeDuration)
    );
  };
  /**
   * @param  {} se
   *
   * Takes in a submit event, shows an error message and start a timeout for when
   * the error message needs to be hidden again. In the case where a previous timeout
   * already exists, replace it with a new timeout to restart the timer
   */
  const handleUserSubmit = (se) => {
    se.preventDefault();

    if (inputText.trim().length < 2) {
      throwInputError("Please enter more than 1 character");
    } else {
      const requestOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          user_response: inputText,
          qid: qid,
          rated: rateState.rated,
          liked: rateState.liked,
          reported: rateState.reported,
        }),
      };

      fetch("/api/questions", requestOptions)
        // .then((response) => response.json())
        .then((data) =>
          onServerResponse
            ? onServerResponse(data)
            : data.json().then((jsonData) => throwInputError(jsonData.error))
        )
        .catch((err) => console.log("Error happened: " + err));

      setRateState({ rated: false, liked: false, reported: false });
    }
    setInputText("");
  };

  /**
   * @param  {} se
   * if the key is a navigation key i.e home, end, arrow keys, etc. they will be
   * prevented from the default behaviour. This is done since the placeholder css
   * cursor cannot move around and reflect the user's navigation
   */
  const handleInputKey = (se) => {
    if (se.keyCode >= 35 && se.keyCode <= 45) {
      se.preventDefault();
    }
  };

  return (
    <div className="user-interact-container" style={style}>
      <form id="user-input-container" onSubmit={handleUserSubmit}>
        <input
          type="text"
          id="input-bar"
          className="input-bar"
          onChange={handleUserType}
          value={inputText}
          maxLength={maxQuestionLength.toString()}
          onKeyDown={handleInputKey}
        />
        <SearchButton mirrored={inputText} goButtonClass={goButtonClass} />
        <div
          className="error-message"
          ref={errorMessageRef}
          hidden={isHidden}
          style={{ opacity: `${+showError}` }}
        ></div>
        <span id="word-count">
          {inputText.length}/{maxQuestionLength}
        </span>
      </form>
      {promptType === "a" && (
        <AnswerRateBar
          handleRate={handleRate}
          rateState={rateState}
          rated={rateState.rated}
          liked={rateState.liked}
          reported={rateState.reported}
        />
      )}
    </div>
  );
};

const SearchButton = ({ mirrored = "", goButtonClass }) => {
  let strLen = Math.max(
    minBoxLength,
    3 + (mirrored.length / maxQuestionLength) * 35
  );

  return (
    <button className={goButtonClass} style={{ width: `${strLen}rem` }}>
      <span id="mirrored-input">{mirrored}</span>
      <HiArrowNarrowRight id="go-button-icon" size={30} />
    </button>
  );
};

const AnswerRateBar = ({ handleRate, liked, rated, reported }) => {
  // const { rated, liked, reported } = rateState;

  const isLiked = rated && liked;
  const isDisliked = rated && !liked;

  return (
    <div className="rate-bar">
      <button
        className="rate-option"
        id="like-option"
        onClick={() => handleRate({ liked: true })}
        style={{ opacity: `${isLiked + 0.3}` }}
      >
        <IoMdThumbsUp className="option-icon" color="#27c840" size={60} />
      </button>

      <button
        className="rate-option"
        id="dislike-option"
        onClick={() => handleRate({ liked: false })}
        style={{ opacity: `${isDisliked + 0.3}` }}
      >
        <IoMdThumbsDown className="option-icon" color="#fe5f57" size={60} />
      </button>

      <button
        className="rate-option"
        id="report-option"
        onClick={() => handleRate({ reported: true })}
        style={{ opacity: `${reported + 0.3}` }}
      >
        <IoMdFlag className="option-icon" color=" #ffbc30" size={60} />
      </button>
    </div>
  );
};
export default UserInput;
